import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const InfoWrap = styled.div`
    width:100%;
    border-top: 1px solid #feaf2b;
    
    .nav{
        margin-top:30px;
        width:980px;
        margin:0 auto;
        a{
            display:inline-block;
            text-decoration:none;
            color:#726454;
            font-size:14px;
            padding:30px 20px;
            &:nth-child(5){
                font-weight:700;
                color:#ff7c98;
            }
        }
    }
`;
const Info = () => {
    return (
        <InfoWrap>
            <div className="nav">
                <Link to="#">점포개설문의</Link>
                <Link to="#">채용문의</Link>
                <Link to="#">윤리신고센터</Link>
                <Link to="#">이용약관</Link>
                <Link to="#">개인정보처리방침</Link>
                <Link to="#">영상정보처리기기운영방침</Link>
                <Link to="#">거래희망회사사전등록</Link>
            </div>
        </InfoWrap>
    );
};

export default Info;